import {Annotator} from "./Annotator/Annotator";
import {Action} from "./Annotator/Action/Action";

const defaultJson = "{\"content\":\"\",\"labelCategories\":[{\"id\":\"0\",\"text\":\"\",\"color\":\"#eac0a2\",\"border-color\":\"#8c7361\"},{\"id\":\"1\",\"text\":\"\",\"color\":\"#619dff\",\"border-color\":\"#3c619d\"},{\"id\":\"2\",\"text\":\"\",\"color\":\"#9d61ff\",\"border-color\":\"#613C9D\"},{\"id\":\"3\",\"text\":\"\",\"color\":\"#ff9d61\",\"border-color\":\"#995e3a\"}],\"labels\":[{\"id\":\"0\",\"categoryId\":\"0\",\"startIndex\":0,\"endIndex\":2},{\"id\":\"1\",\"categoryId\":\"0\",\"startIndex\":3,\"endIndex\":4},{\"id\":\"2\",\"categoryId\":\"0\",\"startIndex\":216,\"endIndex\":217},{\"id\":\"3\",\"categoryId\":\"2\",\"startIndex\":217,\"endIndex\":218},{\"id\":\"4\",\"categoryId\":\"0\",\"startIndex\":219,\"endIndex\":220},{\"id\":\"5\",\"categoryId\":\"2\",\"startIndex\":220,\"endIndex\":221},{\"id\":\"6\",\"categoryId\":\"0\",\"startIndex\":32,\"endIndex\":33},{\"id\":\"7\",\"categoryId\":\"1\",\"startIndex\":46,\"endIndex\":47},{\"id\":\"8\",\"categoryId\":\"0\",\"startIndex\":78,\"endIndex\":80},{\"id\":\"9\",\"categoryId\":\"1\",\"startIndex\":64,\"endIndex\":65}],\"connectionCategories\":[{\"id\":\"0\",\"text\":\"\"},{\"id\":\"1\",\"text\":\"\"},{\"id\":\"2\",\"text\":\"...\"}],\"connections\":[{\"id\":\"0\",\"categoryId\":\"2\",\"fromId\":\"7\",\"toId\":\"6\"},{\"id\":\"1\",\"categoryId\":\"0\",\"fromId\":\"3\",\"toId\":\"2\"},{\"id\":\"2\",\"categoryId\":\"0\",\"fromId\":\"5\",\"toId\":\"4\"},{\"id\":\"3\",\"categoryId\":\"2\",\"fromId\":\"9\",\"toId\":\"6\"}]}";

(window as any).annotator = new Annotator(defaultJson, document.getElementById('svgContainer'));

(window as any).annotator.on('textSelected', (startIndex, endIndex) => {
    (window as any).annotator.applyAction(Action.Label.Create(0, startIndex, endIndex));
});

(window as any).annotator.on('labelClicked', (e) => {
    console.log('labelClicked', e);
});

(window as any).annotator.on('labelRightClicked', (e, x, y) => {
    console.log('labelRightClicked', e, x, y);
    (window as any).annotator.applyAction(Action.Label.Update(e, 1));
});

(window as any).annotator.on('twoLabelsClicked', (id1, id2) => {
    (window as any).annotator.applyAction(Action.Connection.Create(0, id1, id2));
});

(window as any).annotator.on('connectionRightClicked', (e) => {
    (window as any).annotator.applyAction(Action.Connection.Update(e, 1));
});

(window as any).Action = Action;
